<template>
  <div class="nursingrecordprint">
    <div>
      <v-card-text class="mt-3">
        <!-- 畫面顯示內容區域 -->
        <!-- <h3>{{ cData.company_name }}</h3>

        <div class="text-center border-bottom my-2">
          <h1>
            <strong>護理紀錄</strong>
          </h1>
        </div> -->

        <table class="form_title" border="2px #000000 solid;" width="100%">
          <thead>
            <tr>
              <td colspan="5">
                <v-row>
                  <v-col cols="3">
                    <h3>{{ cData.company_name }}</h3>
                  </v-col>
                  <v-col cols="6" class="text-center">
                    <h1>
                      <strong>護理紀錄</strong>
                    </h1>
                  </v-col>
                  <v-col cols="3"></v-col>
                </v-row>
              </td>
            </tr>
            <tr>
              <th colspan="5" class="p-2">
                <v-row>
                  <v-col cols="2">病歷號: {{ uData.p_code }}</v-col>
                  <v-col cols="2">床號: {{uData.bed_name}}</v-col>
                  <v-col cols="2">姓名: {{ uData.name }}</v-col>
                  <v-col cols="2">性別: {{ uData.sex }}</v-col>
                  <v-col cols="2">生日: {{ uData.birthday }}</v-col>
                  <v-col cols="2">年齡: {{ age }}</v-col>
                  <!-- <v-col cols="12">疾病史: {{uData.hadadisease}}</v-col> -->
                </v-row>
              </th>
            </tr>
          </thead>

          <tbody>
            <tr class="text-center font-weight-bold text-nowrap">
              <td>日期</td>
              <td>護理紀錄內容</td>
              <td>項目</td>
              <td>紀錄人</td>
              <td>蓋章</td>
            </tr>

            <tr v-for="(item, index) in items" :key="index">
              <td class="text-nowrap">{{ item.date }} {{ item.time }}</td>
              <td class="text-nowrap">{{ item.project }}</td>
              <td>{{ item.nursingrecord_content }}</td>
              <td class="text-nowrap">{{ item.create_man | filterName }}</td>
              <td width="6%"></td>
            </tr>
          </tbody>
        </table>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions></v-card-actions>
      <!-- <pre>{{ items }}</pre> -->
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      items: [],
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.items = JSON.parse(sessionStorage.getItem("printData"));
  },

  methods: {},
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";
.nursingrecordprint {
  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";
  }

  td {
    padding: 4px;
    border: 1px #000000 solid;
  }
}

@media print {
  .nursingrecordprint {
    .form_title {
      font-size: 1.2rem;
      // text-align: center;
      font-family: "微軟正黑體";
    }

    td {
      padding: 4px;
      border: 1px #000000 solid;
    }

    @page {
      size: A4 portrait;
      margin: 0cm;
      padding: 0cm;
    }
  }
}
</style>